.management-tabs-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 2000;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: fadeIn 0.2s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.management-tabs {
  background: white;
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  padding: 0;
  min-width: 320px;
  max-width: 400px;
  width: 90%;
  animation: slideUp 0.3s ease-out;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.tabs-header {
  background: linear-gradient(135deg, #1976d2 0%, #1565c0 100%);
  color: white;
  padding: 20px 24px 16px;
  border-radius: 12px 12px 0 0;
  text-align: center;
  position: relative;
}

.tabs-header h3 {
  margin: 0;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 0.5px;
}

.close-btn {
  position: absolute;
  top: 12px;
  right: 12px;
  background: rgba(255, 255, 255, 0.2);
  border: none;
  color: white;
  font-size: 18px;
  cursor: pointer;
  padding: 6px;
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.2s ease;
  z-index: 10;
}

.close-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.1);
}

.tabs-content {
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.tab-btn {
  background: linear-gradient(135deg, #2196f3 0%, #1976d2 100%);
  color: white;
  border: none;
  padding: 14px 20px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: 0 2px 8px rgba(33, 150, 243, 0.3);
}

.tab-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(33, 150, 243, 0.4);
  background: linear-gradient(135deg, #42a5f5 0%, #2196f3 100%);
}

.tab-btn:active {
  transform: translateY(0);
}

/* 部署登録ボタン */
.tab-btn:nth-child(1) {
  background: linear-gradient(135deg, #2196f3 0%, #1976d2 100%);
}

.tab-btn:nth-child(1):hover {
  background: linear-gradient(135deg, #42a5f5 0%, #2196f3 100%);
}

/* 社員登録ボタン */
.tab-btn:nth-child(2) {
  background: linear-gradient(135deg, #1976d2 0%, #1565c0 100%);
}

.tab-btn:nth-child(2):hover {
  background: linear-gradient(135deg, #2196f3 0%, #1976d2 100%);
}

/* 設備登録ボタン */
.tab-btn:nth-child(3) {
  background: linear-gradient(135deg, #1565c0 0%, #0d47a1 100%);
}

.tab-btn:nth-child(3):hover {
  background: linear-gradient(135deg, #1976d2 0%, #1565c0 100%);
}

/* テンプレートボタン */
.tab-btn:nth-child(4) {
  background: linear-gradient(135deg, #0d47a1 0%, #01579b 100%);
}

.tab-btn:nth-child(4):hover {
  background: linear-gradient(135deg, #1565c0 0%, #0d47a1 100%);
}
